.custom-radio-input {
    opacity: 0;
    position: absolute;
    width: 0;
    height: 0;
    margin: 0;
    padding: 0;
  }
  
  .custom-radio-container {
    padding-left: 20px; /* Adjust this value as needed to move the radio buttons to the right */
  }
  

/* Custom radio button styling */
.custom-radio-label {
    cursor: pointer;
    position: relative;
    padding-left: 25px; /* Space for custom radio indicator */
    margin-right: 10px;
  }
  
  .custom-radio-label::before {
    content: '';
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 16px; /* Size of custom radio indicator */
    height: 16px;
    border-radius: 50%;
    border: 2px solid #ccc; /* Default border color */
    background-color: white; /* Default background */
  }
  
  /* When the radio input is checked, change the custom indicator color */
  .custom-radio-input:checked + .custom-radio-label::before {
    border-color: #007bff; /* New border color */
    background-color: rgb(21, 255, 0); /* New background color for radio button*/
  }
  